@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic+Coding);
.group {
    margin: 1em auto;
    max-width: 700px;
    padding: 1em;
}

input {
    border: none;
    border-bottom: 2px solid var(--red);
    box-shadow: 0 6px 12px rgba(0,0,0,.23), 0 10px 40px rgba(0,0,0,.19);
    border-radius: 2px;
    font-family: inherit;
    font-size: 20px;
    letter-spacing: 1px;
    padding: 12px;
    transition: border .2s;
    width: 100%;
}

input:active, input:focus {
    border-bottom: 2px solid var(--bonbon);
}

input:hover {
    outline: none;
}

.videoWrapper {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 25px;
    height: 0;
    overflow: hidden;
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
}

.videoWrapper > div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
}

@media only screen and (max-width: 1200px) {
    .videoWrapper {
        border-radius: 10px;
    }
}

.VideoView {
    width: 100%;
    max-width: 1200px;
    display: flex;
    margin: 0 auto;
    /*border: 2px solid #253B6E;*/
}

.VideoOne {
    width: 70%;
}

.VideoList {
    width: 30%;
    min-height: 320px;
    max-height: 497px;
    overflow: auto;
    color:var(--grey);
    background-color: var(--white);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.VideoListItem {
    padding: 15px 30px 15px 15px;
}

.VideoListItem:hover {
    cursor: pointer;
}

.VideoListItem:nth-child(odd) {
    background-color: var(--arsenic);
    color: var(--white);
}

@media only screen and (max-width: 1200px) {
    .VideoView {
        display: block;
        padding: 20px;
    }

    .VideoList, .VideoOne {
        width: 100%;
    }

    .VideoList {
        border-radius: 10px;
        margin-bottom: 50px;
        margin-top: 20px;
    }
}

.Footer {
    color: white;
    background: var(--red);
    text-align: center;
    margin-top: 3em;
    padding: 1em;
    position: absolute;
    bottom: 0;
    width: 100vw;
}

@media only screen and (max-width: 1200px) {
    .Footer {
        position: relative;
    }
}

/*
  Main color palette
*/
:root {
  --black: #000000;
  --grey: #333333;
  --greyLighter: #cccccc;
  --red: #990033;
  --bonbon: #cc9999;
  --white: #ffffff;
  --arsenic: #1e2730;
  --finn: #644e5b;
  --vinrouge: #9e5a63;
  --contessa: #c96967;
  --nepal: #97aabd;
}


body {
  background-image: url(/animeplayer/static/media/rising-sun.3a5f8ce8.png);
  background-size: cover;
  background-repeat: no-repeat;
   background-color: #1e2730;
   background-color: var(--arsenic);
  color: #ffffff;
  color: var(--white);
  font-family: 'Nanum Gothic Coding', monospace;
  font-size: 16;
  margin: 0;
}

a {
  color: #ffffff;
  color: var(--white);
  text-decoration: none;
  outline: none;
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: #ffffff;
  -webkit-tap-highlight-color: var(--white);
}

*:focus {
  outline: none;
}

*:before, *:after {
  box-sizing: border-box;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

h1, h1, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

footer a {
  color: #ffffff;
  color: var(--white);
  border: 1px solid transparent;
  transition: all .3s;
  padding-bottom: 1px;
}

footer a:hover {
  border-bottom-color: #ffffff;
  border-bottom-color: var(--white);
}

